var topBar = avalon.define({
    $id: 'topbar',
    left: 0,
    top: 0,
    layoutKey: NaN,
    slotModeulesKey: '',
    moduleName: '',
    save: function () {
        var page = vm.$model.layouts
        $.when($.ajax({
            type: "POST",
            data: JSON.stringify({"key": "page", "data": page}),
            url: cf.config.uploadUrl, dataType: "json"
        })).then(function (data) {
            alert("")
        }, function () {
            alert("")
        })
    }
})
/*========================  -end ============================*/




/*======================== vm -start ============================*/
